#home_page_content{
  background-color: #e9eaed;
  padding-bottom: 40px;
  .available-info-line-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .content-wrapper{
    width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
  }
  .main-banner{
    background-color: #10412A;
    .main-banner-wrapper{
      img{
        width: 100%;
      }
    }
  }
  .available-info-line{
    height: 46px;
    width: 100%;
    background-color: #00944a;
    font-size: 24px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: -6px;
    .available_links{
      display: flex;
      align-items: center;
      a{
        color: #fff;
        margin: 0 5px;
      }
      span{
        margin-right: 5px;
      }
    }
  }
  h3{
    font-size: 48px;
    text-transform: uppercase;
    margin: 20px 0 10px 0;
    text-align: center;
  }
  .desc_1{
    color: #005038;
    font-size: 24px;
    font-style: italic;
    margin: 10px 0 20px 0;
    text-align: center;
    font-weight: 700;
  }
  .desc_2{
    font-size: 20px;
    text-align: center;
    margin-bottom: 20px;
  }
  .desc_3{
    font-size: 20px;
    margin: 20px 0;
    text-align: center;
    a{
      font-weight: 700;
      color: #005038;
      text-decoration: none;
    }
  }
  .desc_4{
    text-align: center;
    font-size: 20px;
    padding: 20px 0;
    span{
      font-weight: 700;
      text-transform: uppercase;
    }
  }
  .inner-wrapper{
    width: 975px;
    padding: 0 15px;
    margin: 0 auto;
  }
  .buttons-wrapper{
    display: flex;
    justify-content: center;
    button{
      margin: 0 2px;
      color: #fff;
      border-radius: 5px;
      height: 44px;
      font-size: 18px;
      text-transform: capitalize;
      font-weight: 500;
      padding: 14px;
      &.download-button-content{
        background-color: #00944a;
      }
      &.login-button-content{
        background-color: #005038;
      }
    }
  }
  .whats_new_wrapper{
    img{
      width: 100%;
      height: 350px;
    }
    .whats_new_slider{
      margin-top: -6px;
    }
  }
  .MuiContainer-root.MuiContainer-maxWidthLg.css-1oqqzyl-MuiContainer-root{
    padding: 0 !important;
  }
  .video-wrapper{
    iframe{
      width: 100%;
      height: 530px;
    }
  }
}