.detailCard {
    width: 90%;
  div {
    width: 100%;
    display: flex;
    justify-content: center;
    i {
        font-size: 3em;
        color: #016930;
        margin: 0 auto;
        text-align: center;
        cursor: pointer;
      }
  }
  h5 {
    margin-bottom: 15px;
    text-align: center;
    font-weight: 400;
    font-style: italic;
    font-size: 15px;
  }
  p {
    text-align: left !important;
    font-weight: 400;
    font-style: italic;
    font-size: 14px;
  }
  a {
    color: #016930;
    text-decoration: none;
    cursor: pointer;
    font-weight: 400;
    font-style: italic;
    font-size: 15px;
  }
  a:hover {
    color: #333;
  }
}