.home-page-header{
  height: 62px;
  .content-wrapper{
    width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
  }
  .logo{
    img{
      height: 60px;
    }
  }
  .home-page-header-wrapper{
    display: flex;
    justify-content: space-between;
    .menu-items{
      display: flex;
      align-items: center;
      .menu-item{
        text-decoration: none;
        text-transform: uppercase;
        font-weight: bold;
        color: #005038;
      }
      .header-btn{
        color: #fff;
        border-radius: 5px;
        padding: 6px 12px;
        font-weight: bold;
        height: 32px;
        margin: 0 15px;
        &.download-button-header{
          background-color: #00944a;
        }
        &.login-button-header{
          background-color: #005038;
        }
      }
      ul{
        list-style-type: none;
        li{
          display: inline;
          margin: 0 15px;
        }
      }
    }
  }
}