.titlebar {
    width: 100%;
    min-height: 102px;
    max-height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    h5 {
        color: #fff;
        text-align: center;
        font-weight: 400;
        font-style: italic;
        text-shadow: 1px 2px 3px #333;
        font-family: Alegerya !important;
        font-size: 48px;
        font-weight: 700;
    }
    p {
        margin-left: 10px;
        color: #fff;
        text-align: center;
        font-weight: 400;
        font-style: italic;
        text-shadow: 1px 2px 3px #333;
        font-family: Ubuntu,sans-serif;
        font-size: 24px !important;
        margin-bottom: 0px !important;
        margin-top: 10px;
    }
}

.primary {
    background-color: rgba(141,197,62,.8);
}

.secondary {
    background-color: #333;
}